*{
  font-family: 'Roboto Condensed', sans-serif;
}
body{
  padding: 0;
  margin: 0;
}
.app{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
  padding: 0;
  margin: 0;
}
.contador {
  width: 40%;
  text-align: center;
  margin: 0;

}
.contador button{
  display: block;
  width: 100%;
  margin: 1em 0;
  border: none;
  border-radius: 1em;
  padding: .5em 0;
  background: #ccc;
  outline: none;
  cursor: pointer;
}
.contador button:hover{
  background: #bbb;
  font-weight: bold;
}
.alumnos{
  width: 100%;
  text-align: center;
  padding: 0;
  margin: 0;
}
.alumnos input, .alumnos button{
  margin: 0 auto;
  display: block;
  width: 50%;
  padding: .5em 0;
  border: none;
  border-radius: 1em;
  text-align: center;
  outline: none;
  font-size: 15px;
}
.alumnos button{
  background: #ccc;
}
.alumnos button:hover{
  background: #bbb;
  font-weight: bold;
}
.datos{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1em;
  margin: 1em;
}
.datos div{
  background: #ccc;
  border-radius: 1em;
}
.datos div span{
  display: block;
}
.name{
  font-size: 23px;
}
@media (max-width: 650px) {
  .app{
    justify-content: stretch;
  }
  .contador{
    width: 70%;
  }
  .datos{
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: .5em;
    margin: .5em;
  }
  .alumnos input, .alumnos button{
    width: 70%;
  }

}
@media (max-width: 400px) {
  .contador{
    width: 80%;
  }
  .alumnos input, .alumnos button{
    width: 90%;
  }
  .datos{
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: .5em;
    margin: .5em;
  }
}